@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  font-family: "Open Sans", serif;
}

.wrapper {
  background: linear-gradient(to right, #e67e22, #e74c3c);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  height: 90vh;
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  width: 80%;
  margin: 0 auto;
}

.title_div {
  height: 50px;
  width : 1000px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
}



h1{
  font-size: 40px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Roboto Slab', serif;
}

.form_div {
  background-color: #2c3e50;
  height: 550px;
  padding-top: 100px;
  padding-left: 50px;
  width: 1092px;
}



.weather_divs{

  
  width: 950px;
  border-radius: 2px;

  color: white;
  font-family:  "Merriweather", serif;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  
  padding-bottom:  20px;
}

.selection{
  padding-bottom: 20px
  
}

select{
  border-radius: 5px;
}
.dropdownMenu{
  align-items: center;
  border-radius: 5px;
}
.firstDiv{
  padding-left: 20px;
  padding-right: 60px;
  height: 10px;  
  width: fixed; 
}


